* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 18px;
  box-sizing: border-box;
  font-family: "Epilogue", sans-serif;
}

body {
  font-size: 0.8rem;
  max-width: 2500px;
  min-height: 100vh;
  background-color: #ffffff;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.gallery-grid-container {
  display: flex;
  margin-bottom: 5em;
}
@media (min-width: 500px) {
  .gallery-grid-container {
    justify-content: center;
    margin-bottom: 0;
  }
}

.gallery-grid {
  margin-top: 6rem;
  display: grid;
  grid-auto-rows: auto;
  padding: 0;
  gap: 0;
  max-width: 1000px;
}
@media (min-width: 500px) {
  .gallery-grid {
    display: grid;
    grid-template: repeat(3, 400px)/repeat(7, 1fr);
    padding: 0.5rem;
    padding: 1em;
    gap: 1em;
    justify-items: center;
  }
}

.gallery-grid__txt .gallery-grid__heading {
  font-size: 50px;
  max-width: 80%;
}
.gallery-grid__txt .gallery-grid__heading ~ .gallery-grid__blurb {
  font-size: 18px;
  line-height: 28px;
  margin-top: 1em;
  margin-bottom: 1em;
  max-width: 90%;
}

.gallery-grid__txt--one {
  margin-bottom: 1em;
  padding-left: 1em;
}
@media (min-width: 500px) {
  .gallery-grid__txt--one {
    grid-area: 1/1/2/4;
    padding-left: 0;
  }
}
.gallery-grid__txt--one .gallery-grid__heading {
  color: #151515;
}
.gallery-grid__txt--one .gallery-grid__heading:nth-of-type(2) {
  color: #ffffff;
}

.gallery-grid-txt--two {
  background-color: #151515;
  padding: 2em;
}
@media (min-width: 500px) {
  .gallery-grid-txt--two {
    grid-area: 3/5/3/8;
  }
}
.gallery-grid-txt--two .gallery-grid__heading {
  color: #ffffff;
}
.gallery-grid-txt--two .gallery-grid__heading ~ .gallery-grid__blurb {
  color: #ffffff;
}

.gallery-grid__img-container picture .gallery-grid__img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: fill;
  max-width: 100%;
}

@media screen and (min-width: 500px) {
  .gallery-grid__img-container--one {
    grid-area: 1/4/2/8;
    width: 100%;
    height: 100%;
    display: block;
    object-fit: fill;
  }
  .gallery-grid__img-container--two {
    grid-area: 2/5/4/1;
    display: block;
    height: 100%;
    max-width: 100%;
    object-fit: fill;
    width: 100%;
  }
  .gallery-grid__img-container--three {
    grid-area: 2/5/2/8;
    width: 100%;
    display: block;
    height: 100%;
    max-width: 100%;
    object-fit: fill;
    width: 100%;
  }
}
.hero {
  min-height: 100vh;
  width: 100vw;
  display: grid;
  grid-auto-rows: auto;
}
@media (min-width: 500px) and (max-width: 1000px) {
  .hero {
    grid-template: 1fr/repeat(4, 1fr);
  }
}
@media (min-width: 1000px) {
  .hero {
    grid-template: 1fr/repeat(6, 1fr);
    z-index: 100;
  }
}

.main-content__hero-img {
  max-width: 100%;
  width: 100%;
}
@media (min-width: 500px) {
  .main-content__hero-img {
    min-height: 100vh;
  }
}
@media (min-width: 1000px) {
  .main-content__hero-img {
    display: block;
    max-width: 100%;
    min-height: 100vh;
  }
}

@media (min-width: 500px) {
  .hero-imgs {
    grid-area: 1/1/1/3;
  }
}
@media (min-width: 1224px) {
  .hero-imgs {
    grid-area: 1/3/3/5;
  }
}

.main-content__hero-block {
  display: none;
}
@media (min-width: 1000px) {
  .main-content__hero-block {
    display: block;
